/* GLOBAL */

html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  color: #222;
  font-family: -apple-system, BlinkMacSystemFont, “Segoe UI”, Roboto, Oxygen, Ubuntu, Cantarell, “Fira Sans”, “Droid Sans”, “Helvetica Neue”, sans-serif;
}

a {
  text-decoration: none;
}

/* Hide scrollabr in WebKit browsers */
::-webkit-scrollbar {
  display: none;
}

/* App.css */

body {
  background-color: #333;
}

.app {
  height: 667px;
  overflow: hidden;
  position: relative;
  margin: 0 auto;
  width: 375px;
  box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.75);
}

.app__streambg {
  background: #FFF;
  display: block;
}

/* Notifications */
.notifications {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-bottom: 32px;
  padding-left: 0;
  position: absolute;
    bottom: 0;
  width: 100%;
  z-index: 10;
}

/* Notification */
.notification {
  text-align: center;
}
  .notification__inner {
    background-color: #222;
    border-radius: 20px;
    display: inline-block;
    margin: 8px auto 0;
    padding: 10px 16px;
    overflow: hidden;
    vertical-align: baseline;
  }

    .notification__text,
    .notification__icon {
      float: left;
    }

    .notification__text {
      color: #FFF;
      display: block;
      font-size: 16px;
      font-weight: bold;
      white-space: nowrap;
      -webkit-font-smoothing: antialiased;
    }

    .notification__icon {
      display: block;
      margin-left: 8px;
    }

/* ANIMATION */
/* Enter/Leave */
.notifications-enter {
  opacity: 0.01;
  -webkit-transform: translateY(40px);
          transform: translateY(40px);
}

.notifications-enter.notifications-enter-active {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  transition: opacity 150ms ease-in, -webkit-transform 250ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: opacity 150ms ease-in, transform 250ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: opacity 150ms ease-in, transform 250ms cubic-bezier(0.175, 0.885, 0.32, 1.275), -webkit-transform 250ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.notifications-leave {
  opacity: 1;
}

.notifications-leave.notifications-leave-active {
  opacity: 0.01;
  transition: opacity 2s cubic-bezier(0.55, 0.085, 0.68, 0.53);
}

